export default {
  post: [
    {
      id: 1,
      title: 'html5技术将推动移动浏览器变革',
      content: `<blockquote><p>在网络条件日渐成熟的今天，横跨智能手机、平板电脑以及PC终端的HTML5有其无可替代的优势。业内人士分析称，HTML5将为移动互联网带来技术革命，推动移动浏览器变革。
<p>HTML5最大特点：跨平台神奇时代制作人张哲介绍说，HTML5技术是包括HTML、CSS、JavaScript在内的一套技术组合，由谷歌、苹果等几百家公司一起开发，试图将Web带入一个成熟的应用平台。他认为，HTML5最大的特点就是跨平台。一款HTML5游戏，可以随时随地在任何支持HTML5的浏览器上运行。</p>
<p>张哲表示，HTML5的出现，彻底改变了Web网页的呈现方式，为移动互联网带来了技术革命。无论游戏、视频、音乐、阅读还是其他应用，在HTML5的支持下，都可以在浏览器上拥有更好、更便捷的使用体验。比如在微博、微信里，玩家通过分享一个链接，就可以让好友立刻进入游戏，这些是原生游戏做不到的。</p>
<p>峰会上，业内人士分析称，HTML5大潮已经袭来，这项技术对移动互联网的发展将起到非常大的驱动作用。2013年，谷歌发布的Android平台新版ChromeBeta支持HTML5，360宣布将全面推动HTML5发展与普及，三星、中兴、摩托罗拉、火狐等新手机都支持HTML5应用。</p><p>数据显示，2013年全球将有10亿手机浏览器支持HTML5，同时，HTML Web开发者数量将达到200万。毫无疑问，HTML5在未来5-10年内依旧会是移动互联网领域的主宰者。同时，HTML5技术标准的发展与普及意味着世界上将出现功能更强大、更稳定的网络应用.</p><p>有研究机构认为，HTML5兴起，将令苹果的经营利润增长在2015年或遭受30%的损失。同时，苹果生态系统的封闭性也将被打破。苹果采取了拖延策略，为自己下一步布局留出时间。一方面，苹果在iOS设备上强推MPEG格式始终不肯让步；另一方面，苹果甚至从iOS 4.0(手机操作系统)开始，关闭了大量对HTML5重要的API，在iOS上给HTML5应用开发者设置障碍。</p><p>现在Google成了HTML5的最大希望，因为眼下Google对Android的原生生态几乎已失去控制，Google寄希望于HTML5重建一个全新的生态系统。不过，Google却心有余而力不足。</p><p>由于HTML5对复杂的Android硬件终端适应性并不好，像根据屏幕大小、分辨率来调整应用窗口大小的这种基础功能，也往往让开发者无所适从。艾媒咨询首席分析师张毅认为，“考虑到对不同设备的兼容性，目前HTML5的开发成本，不比本地APP降低多少。”而且，HTML5标准制定的分歧也加剧了开发者对兼容性的担忧。”</p></p><p>不过，从另一个角度看，担心HTML5能走多远，可能只是杞人忧天。知名互联网评论家Keso认为：“HTML5的潜能正等待挖掘，未来也许根本看不见一个叫浏览器的东西。”因为HTML5可以打包成APP的形式。换句话说，就算我们不再需要Web或Web应用了，但HTML5仍可作为多种应用开发语言中的一种而存在，只不过再度回到HTML5之前那样的混乱。从这个意义上来说，“2022年”还是值得等待和期待的。</p></p></blockquote>`
    },
    {
      id: 2,
      title: '关于响应式Web设计技巧以及css设置',
      content: `<blockquote><p>html5和css3流行至今，我在做响应式的网站一直是在“尝试”的阶段。并没有深入的去研究和学习，浅显的理解就是根据屏幕分辨率的大小，网站布局、图片、文字大小等相应改变。以后使用平板、手机浏览网站的用户会越来越多，对于前端设计师，学好用html5、css3做响应式网站是势在必行。</p><p>响应式网页设计这个术语，由伊桑马克特提出，他在Alist Apart 发表了一篇开创性的文章，将三种已有的开发技巧（弹性网格布局、弹性图片、媒体和媒体查询）整合起来，并命名为响应式网页布局。还有一些其他叫法，如流式设计、弹性布局、塑料布局、流体设计、自适应布局、跨设备设计以及弹性设计。</p><p>以往我们显示针对桌面电脑来进行宽度设计，然后将其缩小并针对小屏幕进行内容重排；现在我们应该首先针对小屏幕进行设计，然后逐步增强针对大屏幕的设计和内容。</p><p>三个简单步骤，让你的网站变成响应式网站</p><p>ios和Android浏览器都基于Webkit核心。这两种浏览器以及很多其他浏览器如chrome、opera，都支持用viewport meta元素覆盖默认的画布缩放设置，只需要在html的head标签中插入一个meta标签。meta标签中可以设置具体的宽度或者缩放比例。</p><p>示例："meta name="viewport" content="width=device-width,initial-scale=2,maximum-scale=3,user-scalable=no""</p><p>分析：width=device-width告诉浏览器页面的宽度应该等于设备宽度；initial-scale=2页面的缩放比例，设置比例为设备尺寸的2倍；maximum-scale=3,允许用户将页面最多放大至设备宽度的3倍；user-scalable=no禁止用户缩放。缩放是一个重要的辅助功能，所以实践中很少禁用。</p><p>一：粘贴下面的代码到head和/head标签之间:</p><p>meta name="viewport" content="width=device-width,initial-scale=1.0"</p><p>设置比例为1.0这表示浏览器将按照其视口的实际大小来渲染页面</p><p>二：针对不同视口宽度修正设计</p><p>设置viewport meta 标签后，现在我们针对不同视口修正设计效果，创建CSS样式表，并在页面中调用：
常见媒体查询</p><p>/* 平板电脑布局: 481px 至 768px。样式继承自: 移动设备布局。 */</p><p>@media only screen and (min-width: 481px) {</p>
<p>.class{</p><p>background: #333;</p><p>}</p><p>}</p>
<p>/* 桌面电脑布局: 769px 至最高 1232px。样式继承自: 移动设备布局和平板电脑布局。*/</p>
<p>@media only screen and (min-width: 769px) {</p><p>.class {</p><p>background: #666;</p><p>}</p><p>}</p><p>三：字体、弹性图片、视频</p><p>对于响应式网站来说应该有响应式的字体，一个响应式的字体大小应关联它的父容器的宽度，这样它才可以适应客户端的屏幕。CSS3规范引入了一个新的单位叫 rem，和 em 类相似，但相对于 HTML 元素来说， rem 更易于使用。rem 是相对于 HTML 元素的，不要忘了重置 HTML 的字体大小：</p><p>html { font-size:100%; }</p><p>完成后，您可以定义响应式的字体大小，如下所示：<p><p>@media (min-width: 640px) { body {font-size:1rem;} }<p><p>@media (min-width:960px) { body {font-size:1.2rem;} }</p><p>@media (min-width:1100px) { body {font-size:1.5rem;} }</p><p>请注意，旧浏览器不支持 rem 单元，所以不要忘了实现一个替代。</p><p>弹性图片</p><p>我们需要为图片设置max-width:100%和height:auto，来实现其弹性化。对于IE，仍然需要一点额外的工作：</p><p>img{max-width: 100%;height: auto;width: auto\\9; /* ie8 */}弹性内嵌视频同样的，对于视频，我们也需要做max-width： 100%的设置；但是Safari对embed的该属性支持不是很给力，所以我们以width：100%来代替：.video embed,.video object,.video iframe {width: 100%;height: auto;}</p><blockquote>`
    },
    {
      id: 3,
      title: 'JavaScript的特点',
      content: `<blockquote><p>JavaScript是通过嵌入或调入在标准的HTML语言中实现的，其出现弥补了HTML语言的缺陷，是Java与HTML折中的选择。JavaScript具有以下几个基本特点：</p><p>1）JavaScript是一种脚本编写语言，其采用小程序段的方式实现编程。同其他脚本语言一样，JavaScript也是一种解释性语言，其提供了一个非常方便的开发过程。JavaScript的语法基本结构形式与C、C++、Java十分类似。但在使用前，不像这些语言需要先编译，而是在程序运行过程中被逐行地解释。JavaScript与HTML标识结合在一起，从而方便用户的使用操作。</p><p>2）JavaScript是一种基于对象的语言，同时其也可以被看作是一种面向对象的语言，这意味着JavaScript能运用其已经创建的对象。因此，许多功能可以来自于脚本环境中对象的方法与脚本的相互作用。</p><p>3）JavaScript具有简单性。其简单性主要体现在：首先，JavaScript是一种基于Java基本语句和控制流之上的简单而紧凑的设计，从而对于使用者学习Java或其他C语系的编程语言是一种非常好的过渡，而对于具有C语系编程功底的程序员来说，JavaScript上手也非常容易；其次，其变量类型是采用弱类型，并未使用严格的数据类型。</p>
<p>4）JavaScript具有非常高的安全性。JavaScript作为一种安全性语言，不被允许访问本地的硬盘，且不能将数据存入服务器，不允许对网络文档进行修改和删除，只能通过浏览器实现信息浏览或动态交互。从而有效地防止数据的丢失或对系统的非法访问。</p><p>5）JavaScript是动态的，可以直接对用户或客户输入做出响应，无须经过Web服务程序。JavaScript对用户的响应，是以事件驱动的方式进行的。在网页（Web Page）中执行了某种操作所产生的动作，被称为“事件”（Event）。例如按下鼠标、移动窗口、选择菜单等都可以被视为事件。当事件发生后，可能会引起相应的事件响应，执行某些对应的脚本，这种机制被称为“事件驱动”。</p><p>6）JavaScript具有跨平台性。JavaScript依赖于浏览器本身，与操作环境无关，只要计算机能运行浏览器，并支持JavaScript的浏览器，就可正确执行，从而实现了“编写一次，走遍天下”的梦想。</p><p>综合所述，JavaScript是一种新的描述语言，其可以被嵌入到HTML文件中。JavaScript语言可以做到响应使用者的需求事件（例如表单的输入），而不需要任何的网络来回传输资料。所以当一位使用者输入一项资料时，此资料数据不用经过传给服务器（server）处理再传回来的过程，而直接可以被客户端（client）的应用程序所处理。</p><p>1.2   JavaScript与Java的区别</p><p>JavaScript和Java在语法上十分类似，但其有着根本的区别。Java是一种比JavaScript更复杂的程序语言，而JavaScript相对于Java来说，则是相当容易上手的。由于JavaScript程序员可以不太注重程序的编写技巧，所以许多Java的特性在JavaScript中并不被支持。</p><p>虽然JavaScript与Java有紧密的联系，但却是两个公司开发的两个不同的产品。Java是SUN公司推出的新一代面向对象的程序设计语言，特别适合于Internet应用程序开发；而JavaScript是基于Netscape公司的产品，其最初的目的是为了扩展Netscape Navigator功能，而开发的一种可以嵌入Web页面中的基于对象和事件驱动的解释性语言，其前身是Live Script；而Java的前身是Oak语言。下面对两种语言间的异同做如下比较。</p><p>1.2.1   基于对象和面向对象</p><p>Java是一种真正的面向对象的语言，即使是开发简单的程序，也必须设计对象。</p><p>JavaScript是一种脚本语言，可以用来制作与网络无关的，与用户交互作用的复杂软件。由于JavaScript是一种基于对象（Object Based）事件驱动（Event Driver）的编程语言，因而JavaScript本身提供了非常丰富的内部对象供设计人员使用。</p><blockquote>`
    },
    {
      id: 4,
      title: '简述Ajax的优点与缺点',
      content: `<blockquote><p>ajax的优点</p><p>Ajax的给我们带来的好处大家基本上都深有体会，在这里我只简单的讲几点：</p><p>1、最大的一点是页面无刷新，在页面内与服务器通信，给用户的体验非常好。</p><p>2、使用异步方式与服务器通信，不需要打断用户的操作，具有更加迅速的响应能力。</p><p>3、可以把以前一些服务器负担的工作转嫁到客户端，利用客户端闲置的能力来处理，减轻服务器和带宽的负担，节约空间和宽带租用成本。并且减轻服务器的负担，ajax的原则是“按需取数据”，可以最大程度的减少冗余请求，和响应对服务器造成的负担。</p><p>4、基于标准化的并被广泛支持的技术，不需要下载插件或者小程序。</p><p>ajax的缺点</p><p>下面我着重讲一讲ajax的缺陷，因为平时我们大多注意的都是ajax给我们所带来的好处诸如用户体验的提升。而对ajax所带来的缺陷有所忽视。
下面所阐述的ajax的缺陷都是它先天所产生的。</p><p>1、ajax干掉了back按钮，即对浏览器后退机制的破坏。后退按钮是一个标准的web站点的重要功能，但是它没法和js进行很好的合作。这是ajax所带来的一个比较严重的问题，因为用户往往是希望能够通过后退来取消前一次操作的。那么对于这个问题有没有办法？答案是肯定的，用过Gmail的知道，Gmail下面采用的ajax技术解决了这个问题，在Gmail下面是可以后退的，但是，它也并不能改变ajax的机制，它只是采用的一个比较笨但是有效的办法，即用户单击后退按钮访问历史记录时，通过创建或使用一个隐藏的IFRAME来重现页面上的变更。（例如，当用户在Google Maps中单击后退时，它在一个隐藏的IFRAME中进行搜索，然后将搜索结果反映到Ajax元素上，以便将应用程序状态恢复到当时的状态。）但是，虽然说这个问题是可以解决的，但是它所带来的开发成本是非常高的，和ajax框架所要求的快速开发是相背离的。这是ajax所带来的一个非常严重的问题。</p><p>2、安全问题 技术同时也对IT企业带来了新的安全威胁，ajax技术就如同对企业数据建立了一个直接通道。这使得开发者在不经意间会暴露比以前更多的数据和服务器逻辑。ajax的逻辑可以对客户端的安全扫描技术隐藏起来，允许黑客从远端服务器上建立新的攻击。还有ajax也难以避免一些已知的安全弱点，诸如跨站点脚步攻击、SQL注入攻击和基于credentials的安全漏洞等。</p><p>3、对搜索引擎的支持比较弱。</p><p>4、破坏了程序的异常机制。至少从目前看来，像ajax.dll，ajaxpro.dll这些ajax框架是会破坏程序的异常机制的。关于这个问题，我曾经在开发过程中遇到过，但是查了一下网上几乎没有相关的介绍。后来我自己做了一次试验，分别采用ajax和传统的form提交的模式来删除一条数据……给我们的调试带来了很大的困难。</p><p>5、另外，像其他方面的一些问题，比如说违背了url和资源定位的初衷。例如，我给你一个url地址，如果采用了ajax技术，也许你在该url地址下面看到的和我在这个url地址下看到的内容是不同的。这个和资源定位的初衷是相背离的。</p><p>6、一些手持设备（如手机、PDA等）现在还不能很好的支持ajax，比如说我们在手机的浏览器上打开采用ajax技术的网站时，它目前是不支持的，当然，这个问题和我们没太多关系。</p><blockquote>`
    },
    {
      id: 5,
      title: 'jQuery工作原理',
      content: `<blockquote><p>jQuery的模块可以分为3部分：</p><p>入口模块、底层支持模块和功能模块。</p><p>在构造jQuery对象模块中，如果在调用构造函数jQuery()创建jQuery对象时传入了选择器表达式，则会调用选择器Sizzle（一款纯JavaScript实现的CSS选择器引擎，用于查找与选择器表达式匹配的元素集合）遍历文档，查找与之匹配的DOM元素，并创建一个包含了这些DOM元素引用的jQuery对象。</p><p>浏览器功能测试模块提供了针对不同浏览器功能和bug的测试结果，其他模块则基于这些测试结果来解决浏览器之间的兼容性问题。</p><p>在底层支持模块中，回调函数列表模块用于增强对回调函数的管理，支持添加、移除、触发、锁定、禁用回调函数等功能；</p><p>异步队列模块用于解耦异步任务和回调函数，它在回调函数列表的基础上为回调函数增加了状态，并提供了多个回调函数列表，支持传播任意同步或异步回调函数的成功或失败状态；</p><p>数据缓存模块用于为DOM元素和Javascript对象附加任意类型的数据；队列模块用于管理一组函数，支持函数的入队和出队操作，并确保函数按顺序执行，它基于数据缓存模块实现。</p><p>在功能模块中，事件系统提供了统一的事件绑定、响应、手动触发和移除机制，它并没有将事件直接绑定到DOM元素上，而是基于数据缓存模块来管理事件；</p><p>Ajax模块允许从服务器上加载数据，而不用刷新页面，它基于异步队列模块来管理和触发回调函数；<p>动画模块用于向网页中添加动画效果，它基于队列模块来管理和执行动画函数；</p><p>属性操作模块用于对HTML属性和DOM属性进行读取、设置和移除操作；DOM遍历模块用于在DoM树中遍历父元素、子元素和兄弟元素；</p><p>DOM操作模块用于插入、移除、复制和替换DOM元素；样式操作模块用于获取计算样式或设置内联样式；坐标模块用于读取或设置DOM元素的文档坐标；尺寸模块用于获取DOM元素的高度和宽度。</p><p>语言评价</p><p>jQuery 是继prototype 之后又一个优秀的轻量级JavaScript 框架。其宗旨是———“Write Less, Do More”,写更少的代码,做更多的事情。 它是一个快速和简洁的JavaScript 库，可以简化HTML 文档元素的遍历，事件处理，动画和Ajax 交互以实现快速Web 开发，它被设计用来改变编写JavaScript 脚本的方式。</p><p>jQuery 的文档非常丰富，因为其轻量级的特性，文档并不复杂，随着新版本的发布，可以很快被翻译成多种语言，这也为jQuery 的流行提供了条件。jQuery 被包在语法上，jQuery 支持CSS1-3 的选择器， 兼容IE 6.0+, FF 2+,Safari 3.0+, Opera 9.0+, Chrome 等浏览器。同时，jQuery 有约几千种丰富多彩的插件，大量有趣的扩展和出色的社区支持，这弥补了jQuery功能较少的不足并为jQuery 提供了众多非常有用的功能扩展。加之其简单易学，jQuery 很快成为当今最为流行的JavaScript 库，成为开发网站等复杂度较低的Web 应用程序的首选JavaScript 库，并得到了大公司如微软，Google 的支持。</p><p>jQuery 最有特色的语法特点就是与CSS 语法相似的选择器，并且它支持CSS1 到CSS3 的几乎所有选择器，并兼容所有主流浏览器，这为快速访问DOM 提供了方便。</p></blockquote>`
    },
    {
      id: 6,
      title: 'Vue.js基本介绍',
      content: `<blockquote><p>介绍Vue.js是什么</p><p>Vue.js（读音 [表情]juː/，类似于 view） 是一套构建用户界面的渐进式框架。与其他重量级框架不同的是，Vue 采用自底向上增量开发的设计。Vue 的核心库只关注视图层，它不仅易于上手，还便于与第三方库或既有项目整合。另一方面，当与单文件组件和 Vue 生态系统支持的库结合使用时，Vue 也完全能够为复杂的单页应用程序提供驱动。如果你是有经验的前端开发者，想知道 Vue.js与其它库/框架的区别，查看对比其它框架。</p><p>Vue.js 的核心是一个允许采用简洁的模板语法来声明式的将数据渲染进 DOM：</p><p>在接触vue.js之前，前端要用到的dom结构，都是通过拼接字符串的方式进行输出的。这种方式最大的痛点是拼接很麻烦，也不是很直观，几乎无法复用，和数据紧密的偶合在一起，维护不方便，太多太多的问题。在同事的推荐下，开始接触vue.js这个开源项目。如果你不知道什么是vue,那么请看这里的介绍 http://vuejs.org/。</p><p>vue的更新很快，从我最开始接触时的0.11.5，到现在的0.12.7，中文版的介绍一直停留在0.11.5的版本。如果是入门，且英文不是很好的情况下，我建议还是用0.11.5的版本。</p><p>如果你之前有用过angular.js或别的双向绑定的js库,那么你会有一种自来熟的感觉。当然，vue作为一个轻量的前端视图层工具，与angular相比，还是有很明显的区别的：</p><p>1.vue仅仅是mvvm中的view层，只是一个如jquery般的工具库，而不是框架，而angular而是mvvm框架。</p><p>2.vue的双向邦定是基于ES5 中的 getter/setters来实现的，而angular而是由自己实现一套模版编译规则，需要进行所谓的“脏”检查，vue则不需要。因此，vue在性能上更高效，但是代价是对于ie9以下的浏览器无法支持。</p><p>3.vue需要提供一个el对象进行实始化，后续的所有作用范围也是在el对象之下，而angular而是整个html页面。一个页面，可以有多个vue实例，而angular好像不是这么玩的。</p><p>4. vue真的很容易上手，学习成本相对低，不过可以参考的资料不是很丰富，官方文档比较简单，缺少全面的使用案例。高级的用法，需要自己去研究源码，至少目前是这样。</p><p>下面开始写一些的使用经验：</p><p>首先是你要去官网下载vue.js，具体的起步请参考： http://cn.vuejs.org/guide/index.html。</p><p>vue在构建的时候，需要使用new进行实例化，比如:</p> <p>var view = new Vue({ el : '#container'});</p><p>el根据官方api的介绍，可以是String | HTMLElement | Function, 一般来说，传一个id比较常用，这一步就是告诉vue,从这个节点开始，后面的内容就于vue开始接管了。</p><p>接管之后有些什么好处呢?</p><p>1.ajax取出的内容，再也不用拼接好再innerHTML到页面上了，直接在相要出现的地方写上{{数据}}或v-text="数据"或v-html="数据" ，就会自动显示出来。</p><p>2.对于表单来说，特别input之类的元素，不再需要自己监听事件了。这个在手机上的特别方便，不需要判断keycode,也不需要判断event是keyup还是keydown</p><p>利用这个特性，进行表单元素的验证，也是很方便的。结合v-if,v-show,v-style,v-class这些控制器，可以实现，以往我们要写很复杂的ajax或者js才能实现的功能。</p><p>所以这个框架给我们的开发带来了便利，具体一些操作还请大家看一下文档和官网里面的示例。</p></blockquote>`
    }
  ]
}



